'use strict';

async function getData(url) {
    const response = await fetch(url);
    // console.log(response.ok); // true (   )
    // console.log(response.status); //200 (   ,  )
    if (!response.ok) {
        throw new Error(`,    ${url},  : ${response.status}`);
    } //   (   )

    return await response.json();
}

export default getData;